/* NavBar Style */
nav{
  background-color: rgba(127, 255, 212, 0);
}
nav ul{
 
  display: flex;
  margin: 0;
 
}
nav ul li:not(:first-child){
  position: relative;
  margin-top: 20px;
  float: right;
}
nav ul li a{
  text-decoration: none;
  color: #000;
}
nav ul li a:hover{
  color: rgb(216, 26, 26);
  font-weight: 800;
}
nav ul li{
  list-style: none;
  padding: 30px;
  font-size: 15px;
  font-weight: 700;
  
}

nav ul li:not(:first-child):hover{
  color: rgb(216, 26, 26);
  font-weight: 700;
}
.icon{
