}
/* Products Style */
.Products{
  margin: 80px;
  text-align: center;
}
.Products h1{
  font-size: 35px;
  
}
.cards{
  display: flex;
  
}
.Scroll{
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  height: 500px;
  margin-top: 80px;
  width: 100%;
  padding: 10px;

}
 .Scroll::-webkit-scrollbar-thumb {
  background: red; 
  border-radius: 10px;
}
.Scroll::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius:10px;
}

.ltr1{
  color: #b71111;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
/* Card style */
::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 0px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #b71111; 
  border-radius: 0px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}
